@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Press+Start+2P&display=swap);
.cell-container {
  content: '';
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.player-img {
  width: 150px;
  height: 150px;
  border: none;
}

@media screen and (max-width: 700px) {
  .cell-container {
    width: 105px;
    height: 105px;
  }

  .player-img {
    width: 50px;
    height: 50px;
  }
}

button {
  color: white;
  background-color: rgb(40, 40, 43);
  margin: 20px 10px;
  border-radius: 10px;
  padding: 20px;
  cursor: pointer;
  font-size: 20px;
}

button:hover {
  font-size: 25px;
}

.start-btn {
  display: block;
  width: 280px;
}

@media screen and (max-width: 700px) {
  button {
    margin: 5px 5px;
    padding: 15px;
    font-size: 10px;
  }

  .start-btn {
    margin-top: 20px;
    width: 120px;
  }
}

.board-container {
  width: 620px;
  height: auto;
  display: flex;
  justify-items: center;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}

.row-1,
.row-2 {
  content: '';
  width: 620px;
  height: 10px;
  background-color: white;
  position: absolute;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 20px;
}

.row-1 {
  top: calc(33%);
}

.row-2 {
  top: calc(66%);
}

.column-1,
.column-2 {
  content: '';
  width: 10px;
  height: 620px;
  background-color: white;
  position: absolute;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.column-1 {
  left: calc(32%);
}

.column-2 {
  left: calc(64%);
}

.btn-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: rgba(40, 40, 43, 0.3);
  padding: 20px;
  border-radius: 10px;
}

.btn-container h3 {
  margin: 15px 0;
}

.result-title {
  margin: 50px 0;
  font-size: 60px;
}

.active {
  color: black;
  background-color: white;
}

@media screen and (max-width: 700px) {
  .board-container {
    width: 320px;
    height: 320px;
  }

  .column-1,
  .column-2 {
    height: 320px;
  }

  .row-1,
  .row-2 {
    width: 320px;
  }

  .btn-container {
    padding: 40px;
    font-size: 12px;
  }

  .result-title {
    font-size: 40px;
  }
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background: gold;
}

.title {
  color: white;
  margin-bottom: 100px;
  background-color: rgb(40, 40, 43);
  padding: 20px;
  border-radius: 10px;
}

@media screen and (max-width: 700px) {
  .title {
    padding: 20px;
    font-size: 16px;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Press Start 2P', cursive;
}

body {
  width: 100%;
  height: 100vh;
}

